import React from 'react';
import { Link, Route, Switch } from 'react-router-dom';
import { Button, Menu, MenuItem } from '@mui/material';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import Obituary from './components/Obituary';
import Photos from './components/Photos';
import Home from './components/Home';
import './App.css';

function App() {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const handleClick = event => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
        return <Link to="/">Home</Link>;
    };
    return (
        <div style={{ display: 'flex', flexDirection: 'column', alignContent: 'center', justifyContent: 'center' }}>
            <div>
                <Button
                    id="basic-button"
                    aria-controls="basic-menu"
                    aria-haspopup="true"
                    aria-expanded={open ? 'true' : undefined}
                    onClick={handleClick}
                >
                    Menu
                </Button>
                <Menu
                    id="basic-menu"
                    anchorEl={anchorEl}
                    open={open}
                    onClose={handleClose}
                    MenuListProps={{
                        'aria-labelledby': 'basic-button'
                    }}
                >
                    <Link style={{ textDecoration: 'none' }} to="/">
                        <MenuItem onClick={handleClose}>Home</MenuItem>
                    </Link>
                    <Link style={{ textDecoration: 'none' }} to="/obituary">
                        <MenuItem onClick={handleClose}>Obituary</MenuItem>
                    </Link>
                    <Link style={{ textDecoration: 'none' }} to="/photos">
                        <MenuItem onClick={handleClose}>Photos</MenuItem>
                    </Link>
                </Menu>
            </div>
            <Switch>
                <Route exact path={'/'} component={Home} />
                <Route exact path={'/obituary'} component={Obituary} />
                <Route exact path={'/photos'} component={Photos} />
            </Switch>
        </div>
    );
}

export default App;
